.scroll-container {
    position: relative;
    display: block;
    width: 100%;
    max-width: 600px;
    flex: 1;
    padding: 3.2rem 0 11.6rem;
    overflow: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
    ul {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        min-height: 100%;
        overflow-y: scroll;
        list-style: none;
        margin: 0;
        padding: 3.2rem 1.6rem;
    }
}